import React, { useState, useEffect } from "react";
import styled from "styled-components";
import SwiperCore, { Navigation, Pagination, A11y, Autoplay } from "swiper";
import { Swiper, SwiperSlide } from "swiper/react";

import { firebaseapp } from "../../utils";
import "firebase/storage";
import "firebase/database";

import "swiper/swiper.scss";
import "swiper/components/navigation/navigation.scss";
import "swiper/components/pagination/pagination.scss";

SwiperCore.use([Navigation, Pagination, A11y, Autoplay]);

const EPContainer = styled.div`
	padding: ${props => (props.theme.screens.md ? "3vh" : "7vh")};
`;

const TextContainer = styled.div`
	display: flex;
	flex-direction: column;
	align-content: ${props => (props.theme.screens.sm ? "center" : "left")};
	width: ${props => (props.theme.screens.lg ? "100%" : "40vw")};
`;

const TextBig = styled.h1`
	font-family: "Open Sans", sans-serif;
	font-weight: 700;
	font-size: ${props => (props.theme.screens.md ? "2rem" : "2.3rem")};
	text-align: ${props => (props.theme.screens.md ? "center" : "left")};
	display: inline-block;
	border: ${props => (props.theme.screens.md ? "3px solid black" : "none")};
	border-bottom: 3px solid black;
	padding: ${props => (props.theme.screens.md ? "1.2rem" : "0rem 0rem 1.2rem 0rem")};
`;

const TextSmall = styled.h3`
	font-family: "Crimson Text", serif;
	font-size: 1.4rem;
	font-weight: 400;
	text-align: ${props => (props.center ? "center" : "left")};
	margin: 2rem 2rem 2rem 0;
	font-style: ${props => (props.italic ? "italic" : "normal")};
`;

const SwiperContainer = styled.div`
	margin: ${props => (props.theme.screens.md ? "1.2rem" : "0rem")};
`;

const Icons = styled.div`
	color: black;
	font-size: 2rem;

	a {
		color: black;
	}

	a:hover {
		color: ${props => props.theme.colors.black};
	}

	i {
		padding: 1.5rem 1.5rem 0rem 0rem;
	}
`;

const Img = styled.img`
	object-fit: contain;
	max-width: ${props => props.theme.screens.md ? "80vw" : "25vw"};
`;

const EventsAndProjects: React.FC = () => {
	const [imgs, setImgs] = useState([]);
	const [projects, setProjects] = useState([]);

	useEffect(() => {
		firebaseapp
			.storage()
			.ref("fashion_photography/events_projects")
			.listAll()
			.then(({ items }) => {
				let imgRefs = items.map(i => i.getDownloadURL());
				Promise.all(imgRefs).then(ar => {
					setImgs(ar);
				});
			});

		firebaseapp
			.database()
			.ref("fashion_photography/Events and Projects")
			.once("value")
			.then(snapshot => setProjects(snapshot.val()));
	}, []);

	return (
		<EPContainer>
			<TextContainer>
				<TextBig>Events and Projects</TextBig>

				<TextSmall>
					Have a look at some of our events and projects.<br></br>
					Follow us on our instagram and facebook pages to have a look at our other collaborations.
					<Icons>
						<a href="https://www.instagram.com/the.yellow.filter" target="_blank" rel="noopener noreferrer">
							<i className="fab fa-instagram"></i>
						</a>

						<a href="https://www.facebook.com/The-Yellow-Filter-104261981615307/">
							<i className="fab fa-facebook"></i>
						</a>
					</Icons>
				</TextSmall>
			</TextContainer>

			<SwiperContainer>
				<Swiper
					spaceBetween={50}
					slidesPerView={1}
					navigation
					pagination={{ clickable: true }}
					autoplay={{
						delay: 1500,
						disableOnInteraction: false,
					}}
				>
					{imgs.map(url => (
						<SwiperSlide>
							<div style={{ textAlign: "center" }}>
								<Img src={url}></Img>
							</div>
						</SwiperSlide>
					))}
				</Swiper>
			</SwiperContainer>
		</EPContainer>
	);
};

export default EventsAndProjects;
