import React, { useState, useEffect } from "react";
import styled from "styled-components";
import SwiperCore, { Navigation, Pagination, A11y, Autoplay } from "swiper";
import { Swiper, SwiperSlide } from "swiper/react";
import { useTheme } from "../../styles";

import { firebaseapp } from "../../utils";
import "firebase/storage";
import "firebase/database";

import "swiper/swiper.scss";
import "swiper/components/navigation/navigation.scss";
import "swiper/components/pagination/pagination.scss";

SwiperCore.use([Navigation, Pagination, A11y, Autoplay]);

const Container = styled.div`
	padding: 5vw;
`;

const Heading = styled.h1`
	font-family: "Open Sans", sans-serif;
	font-weight: 700;
	font-size: ${props => (props.theme.screens.mmd ? "2.2rem" : "3rem")};
	text-align: center;
	padding: 2rem;
`;

const Text = styled.h4`
	font-family: "Crimson Text", sans-serif;
	font-weight: 400;
	text-align: ${props => (props.center ? "center" : "left")};
	padding: ${props => (props.center ? "0 5vw 0 5vw" : "0")};
`;

const SubHead = styled.h2`
	font-family: "Open Sans", sans-serif;
	font-weight: 700;
	padding: 1.5rem 0 0rem 0;
`;

const Img = styled.img`
	height: 50vh;
`;

const OurTeam: React.FC = () => {
	const { screens } = useTheme();

	const [model_imgs, setModelImgs] = useState([]);
	const [photographer_imgs, setPhotographerImgs] = useState([]);
	const [modelDescriptions, setModelDescriptions] = useState({});
	const [photographerDescriptions, setPhotographerDescriptions] = useState([]);

	useEffect(() => {
		firebaseapp
			.database()
			.ref("fashion_photography/Model Descriptions")
			.once("value")
			.then(snapshot => {
				setModelDescriptions(snapshot.val());
			});

		firebaseapp
			.storage()
			.ref("fashion_photography/models")
			.listAll()
			.then(x => {
				x.prefixes.forEach((ref, index) => {
					ref.listAll().then(({ items }) => {
						Promise.all(items.map(imgRef => imgRef.getDownloadURL())).then(urls => {
							setModelImgs(old => [...old, { urls: urls, index: index }]);
						});
					});
				});
			});

		firebaseapp
			.database()
			.ref("fashion_photography/Photographer Descriptions")
			.once("value")
			.then(snapshot => setPhotographerDescriptions(snapshot.val()));

		firebaseapp
			.storage()
			.ref("fashion_photography/photographers")
			.listAll()
			.then(x => {
				x.prefixes.forEach((ref, index) => {
					ref.listAll().then(({ items }) => {
						Promise.all(items.map(imgRef => imgRef.getDownloadURL())).then(urls => {
							setPhotographerImgs(old => [...old, { urls: urls, index: index }]);
						});
					});
				});
			});
	}, []);

	const getHTML = text => {
		return { __html: text };
	};

	console.log(model_imgs);

	return (
		<Container>
			<Heading>Our Models</Heading>
			<Text center>
				It can be difficult to articulate the power of style and fashion through words. Have a look at some of
				our official models and their portfolios.
			</Text>

			<br></br>
			<br></br>

			{model_imgs.map(model => {
				let description = modelDescriptions[model["index"]];

				if (description == undefined) {
					description = { name: "", summary: "" };
				}

				return (
					<div>
						<SubHead>{description.name}</SubHead>
						<Text dangerouslySetInnerHTML={getHTML(description.summary)}></Text>
						<br></br>
						<br></br>

						<Swiper
							spaceBetween={50}
							slidesPerView={screens.lg ? 1 : 3}
							navigation
							pagination={{ clickable: true }}
							autoplay={{
								delay: 1500,
								disableOnInteraction: false,
							}}
						>
							{model["urls"].map(url => (
								<SwiperSlide>
									<div style={{ textAlign: "center" }}>
										<Img src={url}></Img>
									</div>
								</SwiperSlide>
							))}
						</Swiper>
						<br></br>
						<br></br>
						<br></br>
					</div>
				);
			})}

			<Heading>Our Photographers</Heading>
			<Text center>
				There is one thing the photograph must contain, the humanity of the moment. Our photographers thrive to
				bring out the best of you in every single shot. <br></br>Here are the official Photographers of The
				Yellow Filter.
			</Text>

			<br></br>
			<br></br>

			{photographer_imgs.map(photographer => {
				let description = photographerDescriptions[photographer["index"]];

				if (description == undefined) {
					description = { name: "", summary: "" };
				}

				return (
					<div>
						<SubHead>{description.name}</SubHead>
						<Text dangerouslySetInnerHTML={getHTML(description.summary)}></Text>
						<br></br>
						<br></br>

						<Swiper
							spaceBetween={50}
							slidesPerView={screens.lg ? 1 : 2}
							navigation
							pagination={{ clickable: true }}
							autoplay={{
								delay: 1500,
								disableOnInteraction: false,
							}}
						>
							{photographer["urls"].map(url => (
								<SwiperSlide>
									<div style={{ textAlign: "center" }}>
										<Img src={url}></Img>
									</div>
								</SwiperSlide>
							))}
						</Swiper>
						<br></br>
						<br></br>
						<br></br>
					</div>
				);
			})}
		</Container>
	);
};

export default OurTeam;
