import React, { useState, useEffect } from "react";
import styled from "styled-components";

import { firebaseapp } from "../../utils";
import "firebase/storage";
import "firebase/database";

const Container = styled.div`
	display: flex;
	padding: 5vw;
	flex-direction: ${props => (props.theme.screens.md ? "column" : "row")};
	justify-content: space-around;
	align-items: center;
	background: url("/images/all_circles.svg");
`;

const TextContainer = styled.div`
	padding: 5vw;
`;

const Img = styled.img`
	width: ${props => (props.theme.screens.md ? "90%" : "25vw")};
	padding-bottom: ${props => (props.theme.screens.md ? "8vw" : "0vw")};
	height: 50vh;
	object-fit: cover;
`;

const Heading = styled.h1`
	font-family: "Open Sans", sans-serif;
	color: ${props => props.theme.colors.white};
	font-weight: 700;
	font-size: 3rem;
`;

const SubText = styled.p`
	font-family: "Crimson Text", serif;
	font-weight: 300;
	font-size: 1.5rem;
	color: ${props => props.theme.colors.white};
`;

const ImageContainer = styled.div`
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-auto-rows: auto;
	grid-gap: 1rem;
	flex: 1;
`;

const Home: React.FC = () => {
	const [text, setText] = useState("");

	const [model_imgs, setModelImgs] = useState([]);

	useEffect(() => {
		firebaseapp
			.storage()
			.ref("fashion_photography/home")
			.listAll()
			.then(({ items }) => {
				Promise.all(items.map(imgRef => imgRef.getDownloadURL())).then(urls => {
					setModelImgs(urls);
				});
			});

		firebaseapp
			.database()
			.ref("fashion_photography/Description")
			.once("value")
			.then(snapshot => setText(snapshot.val()));
	}, []);

	const getHTML = () => {
		return { __html: text };
	};

	return (
		<Container>
			<TextContainer>
				<Heading>Fashion and Photography</Heading>
				<SubText dangerouslySetInnerHTML={getHTML()}></SubText>
				<br></br>
			</TextContainer>
			<ImageContainer>
				{model_imgs.map(url => (
					<Img src={url}></Img>
				))}
			</ImageContainer>
		</Container>
	);
};

export default Home;
